body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  /*font-family: Helvetica Neue,Helvetica,PingFang SC,Hiragino Sans GB,Microsoft YaHei,SimSun,sans-serif;*/
  font-family: "SF Pro SC","SF Pro Text","SF Pro Icons","PingFang SC","Helvetica Neue","Helvetica","Arial",sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #888;
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
  display: block;
  /*background-color: #e8ebed;*/
}

/* */

::-webkit-scrollbar
{
  width: 8px;
  height: 8px;
  background-color: #F5F5F5;
}

/* +*/

::-webkit-scrollbar-track
{
  -webkit-box-shadow: inset 0 0 6px #E8EBED;
  border-radius: 10px;
  background-color: #F5F5F5;
}

/* +*/

::-webkit-scrollbar-thumb
{
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px #E8EBED;
  background-color: #ccc;
}

.p-row {
 margin: 0px;
}

.p-col {
 padding: 0px;
}

.p-login {
  width: 100%;
  height: 100%;
  background: url(/static/img/login_bg.0ef66f1.jpeg) no-repeat center;
  background-size: 100% 100%;
}

.p-login-input {
  text-align: center;
  margin-bottom: 20px;
}

.p-login-input input::-webkit-input-placeholder {
  color: white;
}

.p-login-input input,
.p-login-submit-button{
  width: 80%;
  height: 30px;
  line-height: 10px;
  background: transparent;
  outline: none;
  color: white;
  text-align: center;
  border: 3px solid #0095ff;
  border-radius: 20px;
  font-size: 16px;
  letter-spacing: 4px;
  /*display: flex;*/
  margin: 0 auto;
  text-align: center;
}

.p-login-input input:focus,
.p-login-input.has-text input{
  background: rgba(22, 94, 128, 0.67);
}

.p-login-submit-button {
  font-size: 20px;
  background-color: rgba(0,180,255,0.6);
  border: none;
}

.p-header {
  position: fixed;
  width: 100%;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #dfe4ed;
}

.p-logo {
  height: 100%;
  text-align: center;
  width: 180px;
  font-size: 18px;
  font-weight: 700;
  color: #50bfff;
  float: left;
}

.p-header-navbar {
  height: 100%;
  float: left;
}

.p-header-navbar ul {
  height: 100%;
  margin-bottom: 0px;
}

.p-header-navbar ul li {
  height: 100%;
  display: inline-block;
  padding-left: 15px;
  padding-right: 15px;
  cursor: pointer;
}

.p-header-navbar ul li:hover,
.p-header-navbar ul .actived {
  color: #50bfff;
  border-bottom: 3px solid #50bfff;
}

.p-header-right {
  float: right;
  margin-right: 15px;
}

.p-header-right span {
  cursor: pointer;
  padding-left: 10px;
}

.p-full-screen .p-header,
.p-full-screen .p-navside{
  display: none!important;
}

.p-full-screen .p-page-content {
  width: 100%!important;
  height: 100%!important;
  top: 0px!important;
  margin-left: 0px!important;
}

.p-layout .p-navside {
  width: 180px;
}

.p-layout .p-page-content {
  margin-left: 180px;
  width: calc(100% - 180px);
}

.p-no-navside .p-page-content {
  margin-left: 0px!important;
  width: 100%!important;
}

.p-navside {
  position: fixed;
  top: 60px;
  height: calc(100% - 60px);
  border-right: 1px solid #dfe4ed;
  overflow: auto;
  font-size: 14px;
}

.p-navside-toggle {
  background: rgba(221, 221, 221, 0.19);
  text-align: center;
  height: 30px;
  line-height: 30px;
  cursor: pointer;
}

.p-navside .p-menu-item-icon {
  margin-left: 20px;
